<template>
  <div class="gallery">
    <router-link
      class="router-link"
      :to="el.link"
      v-for="el in list"
      :key="el.img"
    >
      <img :src="el.img" alt="" @click="el.methods" />
    </router-link>
  </div>
</template>

<script>
export default {
  name: "GalleryBlock",
  data() {
    return {
      list: [
        {
          link: "/srt1",
          img: require("@/assets/srt/srt1.jpeg"),
          methods: this.addImg1,
        },
        {
          link: "/srt1",
          img: require("@/assets/srt/srt2.jpeg"),
          methods: this.addImg2,
        },
        {
          link: "/srt1",
          img: require("@/assets/srt/srt3.jpeg"),
          methods: this.addImg3,
        },
        {
          link: "/srt1",
          img: require("@/assets/srt/srt4.jpeg"),
          methods: this.addImg4,
        },
        {
          link: "/srt1",
          img: require("@/assets/srt/srt5.jpeg"),
          methods: this.addImg5,
        },
        {
          link: "/srt1",
          img: require("@/assets/srt/srt6.jpeg"),
          methods: this.addImg6,
        },
      ],
    };
  },
  methods: {
    addImg1() {
      localStorage.setItem("sert", 0);
    },
    addImg2() {
      localStorage.setItem("sert", 1);
    },
    addImg3() {
      localStorage.setItem("sert", 2);
    },
    addImg4() {
      localStorage.setItem("sert", 3);
    },
    addImg5() {
      localStorage.setItem("sert", 4);
    },
    addImg6() {
      localStorage.setItem("sert", 5);
    },
  },
};
</script>

